import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { InputOutputInventoryComponent } from './input-output-inventory.component';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { FormsModule } from '@angular/forms';
import { DetailComponent } from './detail/detail.component';
import { IonicModule } from '@ionic/angular';
import { BillDetailModule } from 'src/app/sales/components/bill-detail/bill-detail.module';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';

const routes: Routes = [
  {
    path: '',
    component: InputOutputInventoryComponent
  },
  {
    path: 'detail',
    component: DetailComponent
  },
]

@NgModule({
  declarations: [InputOutputInventoryComponent, DetailComponent, ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TranslateModule,
    NgZorroAntModule,
    FormsModule,
    IonicModule,
    BillDetailModule
  ],
  providers :[VhComponent]
})
export class InputOutputInventoryModule { }
