import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { ImportExportInventoryReport, ImportExportReturnReport } from 'src/app/sales/interface/vh-report';
import { VhType } from 'src/app/sales/interface/vh-type';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-input-output-inventory',
  templateUrl: './input-output-inventory.component.html',
  styleUrls: ['./input-output-inventory.component.scss']
})
export class InputOutputInventoryComponent {
  pageSize: number[] = [20, 30, 40, 50, 100, 200]
  public date: Date[] = [new Date(), new Date()];
  selectedValue: any = 'all';
  filter_value: any = {
    type_product: 'all',
    id_product: 'all',
    type_report: 0
  }
  reportConfig: any = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').show_btax_report_input_output_inventory;
  inOutStock: ImportExportInventoryReport = this.init();
  inOutStock1: ImportExportInventoryReport = this.init();
  inOutStock2: ImportExportInventoryReport = this.init();
  branchList: Array<any> = [] // danh sch chi nhnh show ra HTML;
  search_branchList: Array<any> = []
  id_branch_report: any;
  useBranch: boolean = this.fncService.checkUsingBranch()
  constructor(
    public languageService: LanguageService,
    private vhQuerySales: VhQuerySales,
    public vhComponent: VhComponent,
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private datePipe: DatePipe,
    private vhAuth: VhAuth,
    private fncService: FunctionService
  ) {
    this.date[0].setHours(0, 0, 0, 0)
    this.date[1].setHours(23, 59, 59, 59)
    this.getListProduct()
    this.getBranches()
    if (this.router.getCurrentNavigation().extras.state) {
      let stateSubObj = this.router.getCurrentNavigation().extras.state.stateObj;
      this.inOutStock = stateSubObj.inOutStock;
      this.date = [this.inOutStock.startTime, this.inOutStock.endTime];
      this.id_branch_report = stateSubObj.id_branch_report
      this.filter_value['id_product'] = stateSubObj.selectedValue;
      this.filter_value['type_report'] = stateSubObj.type_report
      //  d liu v li bo co
      if (this.filter_value['type_report'] === 0) this.inOutStock1 = this.inOutStock
      else this.inOutStock2 = this.inOutStock
    }
  }

  heightScroll: string;
  widthScroll: string;

  ngAfterViewChecked() {
    if (document.querySelector(".height-layout") && document.querySelector(".height-header") && document.querySelector(".ant-table-thead") && document.querySelector(".ant-tabs-nav")) {
      this.heightScroll = (document.querySelector(".height-layout").clientHeight -
        document.querySelector(".height-header").clientHeight -
        document.querySelector(".ant-table-thead").clientHeight -
        document.querySelector(".ant-tabs-nav").clientHeight) - 90 + "px";
    }
    if (document.querySelector(".height-layout")) {
      this.widthScroll = document.querySelector(".height-layout").clientWidth + "px";
    }
    this.cdRef.detectChanges();
  }
  listGoods: any = [];
  list_searchGoods: any = [];
  /**
   * get d liu sp v x l phn _id  chn xem report
   * @example this.getListProduct()
   */
  getListProduct() {
    this.listGoods = [{ _id: 'all', name: this.languageService.translate('All') }, ...this.vhAlgorithm.sortStringbyASC(this.vhQuerySales.getlocalDetailProducts(VhType.PRODUCT).filter(e => !e.delete_hidden), 'name')].map(e => { return { ...e, _id: e.id_subproduct ? e.id_subproduct : e._id } })
    this.list_searchGoods = this.listGoods
  }

  /**
   * x l thay i chi nhnh xem bo co
   * @example this.setIdBranch('FDKJWNDIUWHIUDH')
   */
  setIdBranch(value) {
    if (value == "all") {
      this.id_branch_report = []
      for (let i in this.branchList) {
        if (this.branchList[i]._id != 'all') this.id_branch_report.push(this.branchList[i]._id)
      }
    } else this.id_branch_report = [value]
  }


  /**
   * lc sp theo tn v barcode
   * @example this.searchProducts('')
   */
  searchProducts(value) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.listGoods = this.vhAlgorithm.searchList(tempVal, this.list_searchGoods, ['name', 'barcode'])
    else this.listGoods = this.list_searchGoods
  }

  /**
   * set li id_product xem bo co
   */
  filterProducts(id_product) {
    this.filter_value['id_product'] = id_product
    //this.getDataInventory()
  }

  init(startTime: Date = new Date, endTime: Date = new Date): ImportExportInventoryReport {
    startTime.setHours(0, 0, 0, 0)
    endTime.setHours(23, 59, 59, 59)
    return {
      list: [],
      num_total_first: 0,
      total_first_btax: 0,
      total_first_atax: 0,
      num_total_last: 0,
      total_last_btax: 0,
      total_last_atax: 0,
      num_total_in: 0,
      total_in_btax: 0,
      total_in_atax: 0,
      num_total_out: 0,
      total_out_btax: 0,
      total_out_atax: 0,
      startTime,
      endTime
    };
  }

  /**
   * bt sk thay i thi gian xem bo co
   * @param event 
   * @example this.changeTime(['2023-06-27T01:21:41.397Z','2023-06-27T01:21:41.397Z'])
   */
  changeTime(event) {
    if (new Date(event[1].setHours(0, 0, 0, 0)).getTime() - new Date(event[0].setHours(0, 0, 0, 0)).getTime() <= this.fncService.checkDateReport() * 86000000) {
      this.inOutStock['startTime'] = event[0]
      this.inOutStock['endTime'] = event[1]
    } else {
      this.vhComponent.showToast(4000, this.languageService.translate("The time limit to view is") + " " + this.fncService.checkDateReport() + " " + this.languageService.translate("days"), "alert-toast")
      this.date = [new Date(event[0]), new Date(event[0].getTime() + this.fncService.checkDateReport() * 86000000)]
      this.inOutStock['startTime'] = new Date(this.date[0])
      this.inOutStock['endTime'] = new Date(this.date[1])
    }
    this.inOutStock['endTime'].setHours(23, 59, 59, 59)
  }


  goods: any = '1'
  tabs: any = 0

  /**
   * thay i tab xem bo co nhap-xuat-hang or nhap-xuat-ton
   */
  segmentChanged(value) {
    this.filter_value['type_report'] = value
    this.inOutStock = this.init(this.date[0], this.date[1])
  }


  /**
   * get d liu bo co  nhap-xuat-hang or nhap-xuat-ton
   */
  getInventory() {
    return new Promise((resolve, rejects) => {
      if (this.filter_value['type_report'] !== 0) {
        this.vhQuerySales.getReportInputOutputInventory_byTime_byIDProduct(
          this.inOutStock['startTime'],
          this.inOutStock['endTime'],
          this.filter_value['id_product'], this.id_branch_report)
          .then(res => resolve(res)).catch(err => rejects(err))
      } else {
        this.vhQuerySales.getReportInputOutput_byTime_byIDProduct(
          this.inOutStock['startTime'],
          this.inOutStock['endTime'],
          this.filter_value['id_product'], this.id_branch_report)
          .then(res => resolve(res)).catch(err => rejects(err))
      }
    })
  }

  /**
   * xem bo co
   */
  getInOutReturnData() {
    this.vhComponent.showLoadingNotDuration("", "transparent-loading").then(() => {
      this.getInventory()
        .then(res => {
          this.inOutStock['list'] = res['docs'].map((item, index) => { return { ...item, index: index + 1 } })
          if (this.filter_value['type_report'] !== 0) {
            this.inOutStock['num_total_first'] = res['stock_first']
            this.inOutStock['num_total_last'] = res['stock_last']
            this.inOutStock['total_first_atax'] = res['cost_stock_first_atax']
            this.inOutStock['total_first_btax'] = res['cost_stock_first_btax']
            this.inOutStock['total_last_atax'] = res['cost_stock_last_atax']
            this.inOutStock['total_last_btax'] = res['cost_stock_last_btax']
          }

          this.inOutStock['num_total_in'] = res['inquantity']
          this.inOutStock['num_total_out'] = res['outquantity']
          this.inOutStock['total_out_atax'] = res['revenue_atax']
          this.inOutStock['total_out_btax'] = res['revenue_btax']
          this.inOutStock['total_in_atax'] = res['cost_atax']
          this.inOutStock['total_in_btax'] = res['cost_btax']
          if (this.filter_value['type_report'] == 1) {
            this.inOutStock2 = this.inOutStock
            this.sort1(this.statusInquantity, 'inquantity')
          }
          else {
            this.inOutStock1 = this.inOutStock
            this.sort(this.statusInquantity, 'inquantity')
          }

        }).finally(() => this.vhComponent.hideLoading(0))
    })
  }


  //** hm sort cho cc ct */
  statusInquantity = false // cc bin thay i mi tn sort ln xung
  statusAtaxIn = false
  statusBtaxIn = false
  statusOutquantity = false
  statusAtaxOut = false
  statusBtaxOut = false
  statusInquantityS = false
  statusAtaxInS = false
  statusBtaxInS = false
  statusOutquantityS = false
  statusAtaxOutS = false
  statusBtaxOutS = false
  statusName = false
  sort(sort, value) {
    if (sort) {
      this.inOutStock1['list'] = this.vhAlgorithm.sortNumberbyASC([... this.inOutStock1['list']], value).map((item, index) => { return { ...item, index: index + 1 } })
    }
    else {
      this.inOutStock1['list'] = this.vhAlgorithm.sortNumberbyDESC([... this.inOutStock1['list']], value).map((item, index) => { return { ...item, index: index + 1 } })
    }
  }
  sortName(sort, value) {
    if (sort) {
      this.inOutStock1['list'] = this.vhAlgorithm.sortVietnamesebyASC([... this.inOutStock1['list']], value).map((item, index) => { return { ...item, index: index + 1 } })
    }
    else {
      this.inOutStock1['list'] = this.vhAlgorithm.sortVietnamesebyDESC([... this.inOutStock1['list']], value).map((item, index) => { return { ...item, index: index + 1 } })
    }
  }
  sort1(sort, value) {
    if (sort) {
      this.inOutStock2['list'] = this.vhAlgorithm.sortNumberbyASC([... this.inOutStock2['list']], value).map((item, index) => { return { ...item, index: index + 1 } })
    }
    else {
      this.inOutStock2['list'] = this.vhAlgorithm.sortNumberbyDESC([... this.inOutStock2['list']], value).map((item, index) => { return { ...item, index: index + 1 } })
    }
  }
  sortName1(sort, value) {
    if (sort) {
      this.inOutStock2['list'] = this.vhAlgorithm.sortVietnamesebyASC([... this.inOutStock2['list']], value).map((item, index) => { return { ...item, index: index + 1 } })
    }
    else {
      this.inOutStock2['list'] = this.vhAlgorithm.sortVietnamesebyDESC([... this.inOutStock2['list']], value).map((item, index) => { return { ...item, index: index + 1 } })
    }
  }

  goToDetail(item) {
    let stateObj = {
      id_branch_report: this.id_branch_report,
      selectedValue: this.filter_value['id_product'], type_report: this.filter_value.type_report, inOutStock: this.inOutStock
    }
    setTimeout(() => {
      this.router.navigate(["/sales/dashboard/manage/reports/input-output-inventory/detail"], {
        state: {
          detail: { ...item, type_report: this.filter_value['type_report'] },
          stateObj: stateObj
        }
      })
    }, 100)
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/reports']);
  }

  onCurrentPageDataChange(value) {
  }
  /**--------------------- */
  /**zorro modal */
  checked: boolean = true;
  isVisible = false;
  showModal(): void {
    this.filter_value['goods'] = this.goods
    this.filter_value['id_product'] = 'all'
    this.isVisible = true;
  }
  handleOk(): void {
    this.getListProduct()
    this.isVisible = false;
  }
  handleCancel(): void {
    this.isVisible = false;
  }
  /**
   * 
   * @param date 
   * @returns string
   * @example let date = this.format_date('2023-06-27T01:10:34.396Z') => output '27_06_2023'
   */
  format_date(date) {
    return this.datePipe.transform(date, "dd_MM_yyyy")
  }

  /**
   * get chi nhnh c xem bo co ca user
   */
  getBranches() {
    let branches = [...this.vhQuerySales.getlocalBranchs()] // danh sch ton b chi nhnh
    let employee = this.vhAuth.getUser() // thng tn nhn vin
    let defaultBranch = this.vhQuerySales.getDefaultBranch()._id // chi nhnh mc nh khi ng nhp (ly id chi nhnh)

    if (this.useBranch) { // kim tra xem c 2 chi nhnh tr ln

      if (employee['owner'] == 'boss') { // kim tra ti khon ng nhp l boss
        this.branchList = branches // gn danh sch chi nhnh show ra HTML
        if (this.branchList.length > 1) this.branchList.unshift({ _id: "all", name: "All" })
        this.id_branch_report = defaultBranch // gn mc nh chi nhnh mc nh xem bo co l chi nhnh ng nhp v
      }

      else { // trng hp ti khon l nhn vin
        this.vhAuth.getPermission(employee['_id']).then(result => { // get quyn nhn vin c xem bo co chi nhnh no
          branches.forEach(item => {
            if (result[`${item._id}_view_report_branch`]) this.branchList.push({ ...item }) // kim tra chi nhnh c xem bo co, gn cho branchList show ra HMLT
          })
          if (this.branchList.length > 1) { // trng hp c xem bo co 2 chi nhnh tr ln
            this.branchList.unshift({ _id: "all", name: "All" }) // y xem tt c v, nu c 2 chi nhnh tr ln
            for (let i in this.branchList) {
              if (this.branchList[i]._id == defaultBranch) return this.id_branch_report = defaultBranch // nu c gn chi nhnh xem bo mc nh l chi nhnh mc nh (ng nhp)
              else this.id_branch_report = this.branchList[1]._id // ngc li gn chi nhnh mc nh l chi nhnh th 1
            }
          }
          else if (this.branchList.length == 1) { // nu ch c xem bo co 1 chi nhnh , ly mc nh l chi nhnh 
            this.id_branch_report = this.branchList[0]._id
          }
          else this.vhComponent.alertMessageDesktop("warning", "Bn khng c chn chi nhnh xem bo co") // kg c chi nhnh xem bo co
        })

      }
      this.search_branchList = this.branchList
    } else this.id_branch_report = defaultBranch // ch xi 1 chi nhnh
  }

  /**
   * lc chi nhnh theo tn
   * @param value 
   */
  search(value) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.branchList = this.vhAlgorithm.searchList(tempVal, this.search_branchList, ['name'])
    else this.branchList = this.search_branchList
  }

  status: boolean = false;
  /**
   * xut d liu sang file excel v ti v
   */
  export() {
    // x l to tn file
    let data = new Array();
    let date =
      this.languageService.translate("_from") + "_" +
      this.format_date(this.inOutStock['startTime']) + "_" +
      this.languageService.translate("_to") + "_" +
      this.format_date(this.inOutStock['endTime']);
    let name = `${this.languageService.translate(this.filter_value.type_report == 1 ? "_input_output_inventory_report" : "_input_output_items_report")}_${date}_${this.id_branch_report == "all" ? "" : this.vhAlgorithm.changeAlias(this.vhQuerySales.getlocalBranch(this.id_branch_report).name).replace(/\s/g, "_")}`;
    // thm dng u tin 
    let title: any = new Object();
    title[this.languageService.translate("No.")] = "";
    title[this.languageService.translate("Product name")] = "";
    title[this.languageService.translate("Unit")] = "";
    title[this.languageService.translate("Barcode")] = "";
    if (this.filter_value['type_report'] == 1) {
      title[this.languageService.translate("Opening stock")] = this.languageService.translate("Quantity");
      if (this.reportConfig) title[""] = this.languageService.translate("Amount before tax");
      title[" "] = this.languageService.translate("Amount after tax");
    }
    title[this.languageService.translate("Purchased in period")] = this.languageService.translate("Quantity");
    if (this.reportConfig) title["  "] = this.languageService.translate("Amount before tax");
    title["   "] = this.languageService.translate("Amount after tax");
    title[this.languageService.translate("Issued in period")] = this.languageService.translate("Quantity");
    if (this.reportConfig) title["    "] = this.languageService.translate("Amount before tax");
    title["     "] = this.languageService.translate("Amount after tax");
    if (this.filter_value['type_report'] == 1) {
      title[this.languageService.translate("Closing stock")] = this.languageService.translate("Quantity");
      if (this.reportConfig) title["      "] = this.languageService.translate("Amount before tax");
      title["       "] = this.languageService.translate("Amount after tax");
      // name = this.languageService.translate("Stock out/in Report") + "_" + this.languageService.translate("From") + "_" + date;
    }
    data.push(title);
    // dng hin th tng
    let total: any = new Object();
    total[this.languageService.translate("No.")] = "";
    total[this.languageService.translate("Product name")] = this.languageService.translate("Total");
    total[this.languageService.translate("Unit")] = "";
    total[this.languageService.translate("Barcode")] = "";
    if (this.filter_value['type_report'] == 1) {
      total[this.languageService.translate("Opening stock")] = this.inOutStock.num_total_first;
      if (this.reportConfig) total[""] = this.inOutStock.total_first_btax;
      total[" "] = this.inOutStock.total_first_atax;
    }
    total[this.languageService.translate("Purchased in period")] = this.inOutStock.num_total_in;
    if (this.reportConfig) total["  "] = this.inOutStock.total_in_btax;
    total["   "] = this.inOutStock.total_in_atax;
    total[this.languageService.translate("Issued in period")] = this.inOutStock.num_total_out;
    if (this.reportConfig) total["    "] = this.inOutStock.total_out_btax;
    total["     "] = this.inOutStock.total_out_atax;
    if (this.filter_value['type_report'] == 1) {
      total[this.languageService.translate("Closing stock")] = this.inOutStock.num_total_last;
      if (this.reportConfig) total["      "] = this.inOutStock.total_last_btax;
      total["       "] = this.inOutStock.total_last_atax;
    }
    data.push(total);

    for (let _rows of this.inOutStock.list) {
      let item: any = new Object();
      item[this.languageService.translate("No.")] = _rows.index;
      item[this.languageService.translate("Product name")] = _rows.name;
      item[this.languageService.translate("Unit")] = _rows.unit;
      item[this.languageService.translate("Barcode")] = _rows.barcode;
      if (this.filter_value['type_report'] == 1) {
        item[this.languageService.translate("Opening stock")] = _rows.stock_first;
        if (this.reportConfig) item[""] = _rows.cost_stock_first_btax;
        item[" "] = _rows.cost_stock_first_atax;
      }
      item[this.languageService.translate("Purchased in period")] = _rows.inquantity;
      if (this.reportConfig) item["  "] = _rows.cost_btax;
      item["   "] = _rows.cost_atax;
      item[this.languageService.translate("Issued in period")] = _rows.outquantity;
      if (this.reportConfig) item["    "] = _rows.revenue_btax;
      item["     "] = _rows.revenue_atax;
      if (this.filter_value['type_report'] == 1) {
        item[this.languageService.translate("Closing stock")] = _rows.stock_last;
        if (this.reportConfig) item["      "] = _rows.cost_stock_last_btax;
        item["       "] = _rows.cost_stock_last_atax;
      }

      data.push(item);
      for (let lot of _rows.lots ? _rows.lots : []) {
        let data_lot: any = new Object();
        data_lot[this.languageService.translate("No.")] = "";
        data_lot[this.languageService.translate("Product name")] = lot.lot_number;
        data_lot[this.languageService.translate("Unit")] = this.format_date(new Date(lot.date_mfg)) + ' - ' + this.format_date(new Date(lot.date_exp));
        
        data_lot[this.languageService.translate("Barcode")] = lot.barcode;
        if (this.filter_value['type_report'] == 1) {
          data_lot[this.languageService.translate("Opening stock")] = lot.stock_first;
          if (this.reportConfig) data_lot[""] = lot.cost_stock_first_btax;
          data_lot[" "] = lot.cost_stock_first_atax;
        }
        data_lot[this.languageService.translate("Purchased in period")] = lot.inquantity;
        if (this.reportConfig) data_lot["  "] = lot.cost_btax;
        data_lot["   "] = lot.cost_atax;
        data_lot[this.languageService.translate("Issued in period")] = lot.outquantity;
        if (this.reportConfig) data_lot["    "] = lot.revenue_btax;
        data_lot["     "] = lot.revenue_atax;
        if (this.filter_value['type_report'] == 1) {
          data_lot[this.languageService.translate("Closing stock")] = lot.stock_last;
          if (this.reportConfig) data_lot["      "] = lot.cost_stock_last_btax;
          data_lot["       "] = lot.cost_stock_last_atax;
        }

        data.push(data_lot);
      }
    }
    this.vhAlgorithm.exportXLSX(data, name)
  }
}
